'use client';

import { useState, useEffect, useRef } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Skeleton } from '@/components/ui/skeleton';
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Search, Calendar, Tag, Filter, Trash2 } from 'lucide-react';

interface Note {
  id: string;
  title: string;
  content: string;
  summary?: string;
  tags: string[];
  categories: string[];
  createdAt: string;
  updatedAt: string;
}

export function NotesList() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const [notes, setNotes] = useState<Note[]>([]);
  const [loading, setLoading] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [selectedTag, setSelectedTag] = useState('all');
  
  // Client-side cache
  const cacheRef = useRef<Map<string, { data: Note[]; timestamp: number }>>(new Map());
  const CACHE_DURATION = 30000; // 30 seconds
  
  // Delete dialog state
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [noteToDelete, setNoteToDelete] = useState<Note | null>(null);

  // Initialize state from URL parameters
  useEffect(() => {
    const search = searchParams.get('search') || '';
    const category = searchParams.get('category') || 'all';
    const tag = searchParams.get('tag') || 'all';
    
    setSearchTerm(search);
    setSelectedCategory(category);
    setSelectedTag(tag);
  }, [searchParams]);

  const fetchNotes = async (showLoading = true) => {
    try {
      if (showLoading) setLoading(true);
      
      const params = new URLSearchParams();
      if (searchTerm) params.append('search', searchTerm);
      if (selectedCategory && selectedCategory !== 'all') params.append('category', selectedCategory);
      if (selectedTag && selectedTag !== 'all') params.append('tag', selectedTag);
      
      const cacheKey = params.toString();
      const now = Date.now();
      
      // Check cache first
      const cached = cacheRef.current.get(cacheKey);
      if (cached && (now - cached.timestamp) < CACHE_DURATION) {
        setNotes(cached.data);
        if (showLoading) setLoading(false);
        return;
      }

      const response = await fetch(`/api/notes?${cacheKey}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
        cache: 'default', // Allow browser caching
        next: { revalidate: 60 }, // Revalidate every 60 seconds
      });
      
      if (response.ok) {
        const data = await response.json();
        setNotes(data);
        
        // Cache the result
        cacheRef.current.set(cacheKey, {
          data,
          timestamp: now
        });
      } else {
        console.error('Failed to fetch notes:', response.status, response.statusText);
        setNotes([]);
      }
    } catch (error) {
      console.error('Error fetching notes:', error);
      setNotes([]);
    } finally {
      if (showLoading) setLoading(false);
    }
  };

  useEffect(() => {
    fetchNotes();
  }, []);

  useEffect(() => {
    const timeoutId = setTimeout(() => {
      fetchNotes(false); // Don't show loading for search/filter changes
    }, 300); // Optimized debounce time for better performance
    return () => clearTimeout(timeoutId);
  }, [searchTerm, selectedCategory, selectedTag]);

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  const handleViewNote = (noteId: string) => {
    // Create return URL with current search parameters
    const currentUrl = new URL(window.location.href);
    const returnUrl = currentUrl.pathname + currentUrl.search;
    router.push(`/notes/${noteId}?return=${encodeURIComponent(returnUrl)}`);
  };

  const handleDeleteClick = (note: Note) => {
    setNoteToDelete(note);
    setDeleteDialogOpen(true);
  };

  const handleDeleteConfirm = async () => {
    if (!noteToDelete) return;

    try {
      const response = await fetch(`/api/notes/${noteToDelete.id}`, {
        method: 'DELETE',
      });

      if (response.ok) {
        // Remove from local state and refresh cache
        setNotes(notes.filter(note => note.id !== noteToDelete.id));
        cacheRef.current.clear(); // Clear cache to force refresh
        setDeleteDialogOpen(false);
        setNoteToDelete(null);
      } else {
        alert('Failed to delete note');
      }
    } catch (err) {
      console.error('Error deleting note:', err);
      alert('Failed to delete note');
    }
  };

  const updateURLParams = (newSearchTerm: string, newCategory: string, newTag: string) => {
    const params = new URLSearchParams(searchParams.toString());
    
    // Update view parameter to ensure we stay on review page
    params.set('view', 'review');
    
    if (newSearchTerm) {
      params.set('search', newSearchTerm);
    } else {
      params.delete('search');
    }
    
    if (newCategory && newCategory !== 'all') {
      params.set('category', newCategory);
    } else {
      params.delete('category');
    }
    
    if (newTag && newTag !== 'all') {
      params.set('tag', newTag);
    } else {
      params.delete('tag');
    }
    
    router.push(`/?${params.toString()}`, { scroll: false });
  };

  const handleSearchChange = (value: string) => {
    setSearchTerm(value);
    updateURLParams(value, selectedCategory, selectedTag);
  };

  const handleCategoryChange = (value: string) => {
    setSelectedCategory(value);
    updateURLParams(searchTerm, value, selectedTag);
  };

  const handleTagChange = (value: string) => {
    setSelectedTag(value);
    updateURLParams(searchTerm, selectedCategory, value);
  };

  const allCategories = Array.from(new Set(notes.flatMap(note => note.categories)));
  const allTags = Array.from(new Set(notes.flatMap(note => note.tags)));

  // Show loading only for initial load, not for search/filter changes
  const showInitialLoading = loading && notes.length === 0;

  const SkeletonRow = () => (
    <tr className="border-t">
      <td className="p-4">
        <div className="space-y-2">
          <Skeleton className="h-4 w-48" />
          <Skeleton className="h-3 w-64" />
        </div>
      </td>
      <td className="p-4">
        <Skeleton className="h-3 w-40" />
      </td>
      <td className="p-4">
        <div className="flex gap-1">
          <Skeleton className="h-5 w-12 rounded-full" />
          <Skeleton className="h-5 w-16 rounded-full" />
        </div>
      </td>
      <td className="p-4">
        <div className="flex gap-1">
          <Skeleton className="h-5 w-20 rounded-full" />
        </div>
      </td>
      <td className="p-4">
        <Skeleton className="h-3 w-24" />
      </td>
      <td className="p-4">
        <Skeleton className="h-8 w-16" />
      </td>
    </tr>
  );

  if (showInitialLoading) {
    return (
      <div className="h-full w-full p-4 overflow-hidden">
        <Card className="h-full flex flex-col">
          <CardHeader className="flex-shrink-0">
            <CardTitle className="flex items-center gap-2">
              <Search className="w-5 h-5" />
              Review Notes
            </CardTitle>
            <CardDescription>
              Search and filter through your saved notes
            </CardDescription>
          </CardHeader>
          <CardContent className="flex-1 flex flex-col space-y-6 overflow-hidden">
            {/* Search and Filters Skeleton */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 flex-shrink-0">
              <Skeleton className="h-10 w-full" />
              <Skeleton className="h-10 w-full" />
              <Skeleton className="h-10 w-full" />
            </div>

            {/* Table Skeleton */}
            <div className="flex-1 border rounded-lg overflow-hidden flex flex-col">
              <div className="flex-1 overflow-auto">
                <table className="w-full">
                  <thead className="bg-muted">
                    <tr>
                      <th className="text-left p-4 font-medium">Title</th>
                      <th className="text-left p-4 font-medium">Summary</th>
                      <th className="text-left p-4 font-medium">Tags</th>
                      <th className="text-left p-4 font-medium">Categories</th>
                      <th className="text-left p-4 font-medium">Created</th>
                      <th className="text-left p-4 font-medium">Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {Array.from({ length: 5 }).map((_, index) => (
                      <SkeletonRow key={index} />
                    ))}
                  </tbody>
                </table>
              </div>
            </div>

            {/* Results Summary Skeleton */}
            <div className="text-sm text-muted-foreground flex-shrink-0">
              <Skeleton className="h-4 w-48" />
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="h-full w-full p-4 overflow-hidden">
      <Card className="h-full flex flex-col">
        <CardHeader className="flex-shrink-0">
          <CardTitle className="flex items-center gap-2">
            <Search className="w-5 h-5" />
            Review Notes
          </CardTitle>
          <CardDescription>
            Search and filter through your saved notes
          </CardDescription>
        </CardHeader>
        <CardContent className="flex-1 flex flex-col space-y-6 overflow-hidden">
      {/* Search and Filters */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 flex-shrink-0">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground w-4 h-4" />
              <Input
                placeholder="Search notes..."
                value={searchTerm}
                onChange={(e) => handleSearchChange(e.target.value)}
                className="pl-10"
              />
            </div>
            <div className="relative">
              <Filter className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground w-4 h-4 z-10" />
              <Select value={selectedCategory} onValueChange={handleCategoryChange}>
                <SelectTrigger className="w-full pl-10">
                  <SelectValue placeholder="All Categories" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All Categories</SelectItem>
                  {allCategories.map(category => (
                    <SelectItem key={category} value={category}>
                      {category}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            <div className="relative">
              <Tag className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground w-4 h-4 z-10" />
              <Select value={selectedTag} onValueChange={handleTagChange}>
                <SelectTrigger className="w-full pl-10">
                  <SelectValue placeholder="All Tags" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All Tags</SelectItem>
                  {allTags.map(tag => (
                    <SelectItem key={tag} value={tag}>
                      {tag}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
      </div>

          {/* Notes Table */}
          <div className="flex-1 border rounded-lg overflow-hidden flex flex-col">
            <div className="flex-1 overflow-auto">
              <table className="w-full">
                <thead className="bg-muted">
                  <tr>
                    <th className="text-left p-4 font-medium">Title</th>
                    <th className="text-left p-4 font-medium">Summary</th>
                    <th className="text-left p-4 font-medium">Tags</th>
                    <th className="text-left p-4 font-medium">Categories</th>
                    <th className="text-left p-4 font-medium">Created</th>
                    <th className="text-left p-4 font-medium">Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {notes.length === 0 ? (
                    <tr>
                      <td colSpan={6} className="text-center p-8 text-muted-foreground">
                        No notes found. Start by taking some notes!
                      </td>
                    </tr>
                  ) : (
                    notes.map((note) => (
                      <tr key={note.id} className="border-t hover:bg-muted/50">
                        <td className="p-4">
                          <div className="font-medium">{note.title}</div>
                          <div className="text-sm text-muted-foreground truncate max-w-xs">
                            {note.content.substring(0, 100)}...
                  </div>
                        </td>
                        <td className="p-4">
                          <div className="text-sm text-muted-foreground max-w-xs">
                            {note.summary || 'No summary available'}
                </div>
                        </td>
                        <td className="p-4">
                  <div className="flex flex-wrap gap-1">
                            {note.tags.slice(0, 3).map((tag) => (
                              <Badge key={tag} variant="secondary" className="text-xs">
                        {tag}
                              </Badge>
                    ))}
                            {note.tags.length > 3 && (
                              <Badge variant="outline" className="text-xs">
                                +{note.tags.length - 3}
                              </Badge>
                            )}
                  </div>
                        </td>
                        <td className="p-4">
                  <div className="flex flex-wrap gap-1">
                            {note.categories.slice(0, 2).map((category) => (
                              <Badge key={category} variant="outline" className="text-xs">
                        {category}
                              </Badge>
                    ))}
                            {note.categories.length > 2 && (
                              <Badge variant="outline" className="text-xs">
                                +{note.categories.length - 2}
                              </Badge>
                            )}
                  </div>
                        </td>
                        <td className="p-4">
                          <div className="flex items-center gap-1 text-sm text-muted-foreground">
                            <Calendar className="w-3 h-3" />
                            {formatDate(note.createdAt)}
                </div>
                        </td>
                        <td className="p-4">
                          <div className="flex gap-2">
                            <Button 
                              variant="outline" 
                              size="sm"
                              onClick={() => handleViewNote(note.id)}
                            >
                              View
                            </Button>
                            <Dialog open={deleteDialogOpen && noteToDelete?.id === note.id} onOpenChange={(open) => {
                              if (!open) {
                                setDeleteDialogOpen(false);
                                setNoteToDelete(null);
                              }
                            }}>
                              <DialogTrigger asChild>
                                <Button 
                                  variant="outline" 
                                  size="sm"
                                  onClick={() => handleDeleteClick(note)}
                                >
                                  <Trash2 className="h-4 w-4" />
                                </Button>
                              </DialogTrigger>
                              <DialogContent>
                                <DialogHeader>
                                  <DialogTitle>Delete Note</DialogTitle>
                                  <DialogDescription>
                                    Are you sure you want to delete "{note.title}"? This action cannot be undone.
                                  </DialogDescription>
                                </DialogHeader>
                                <DialogFooter>
                                  <Button variant="outline" onClick={() => {
                                    setDeleteDialogOpen(false);
                                    setNoteToDelete(null);
                                  }}>
                                    Cancel
                                  </Button>
                                  <Button variant="destructive" onClick={handleDeleteConfirm}>
                                    <Trash2 className="h-4 w-4 mr-2" />
                                    Delete
                                  </Button>
                                </DialogFooter>
                              </DialogContent>
                            </Dialog>
                          </div>
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
              </div>
          </div>

          {/* Results Summary */}
          <div className="text-sm text-muted-foreground flex-shrink-0 flex items-center justify-between">
            <span>
              Showing {notes.length} note{notes.length !== 1 ? 's' : ''}
              {searchTerm && ` matching "${searchTerm}"`}
              {selectedCategory && selectedCategory !== 'all' && ` in category "${selectedCategory}"`}
              {selectedTag && selectedTag !== 'all' && ` with tag "${selectedTag}"`}
            </span>
            {loading && notes.length > 0 && (
              <div className="flex items-center gap-2">
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-primary"></div>
                <span className="text-xs">Updating...</span>
              </div>
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}