'use client';

import { useState } from 'react';
import { Save, Mic, Bot } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';

export function NoteTaker() {
  const [transcript, setTranscript] = useState('');
  const [manualText, setManualText] = useState('');
  const [title, setTitle] = useState('');
  const [tags, setTags] = useState<string[]>([]);
  const [categories, setCategories] = useState<string[]>([]);
  const [isProcessing, setIsProcessing] = useState(false);

  const saveNote = async () => {
    const contentToSave = manualText.trim() || transcript.trim();
    if (!contentToSave) return;

    setIsProcessing(true);
    try {
      const response = await fetch('/api/notes', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          title: title || 'Untitled Note',
          content: contentToSave,
          transcript: transcript || null,
          tags: tags.length > 0 ? tags : ['note'],
          categories: categories.length > 0 ? categories : ['general'],
        }),
      });

      if (!response.ok) {
        throw new Error('Failed to save note');
      }

      await response.json();

      // Reset form
      setTranscript('');
      setManualText('');
      setTitle('');
      setTags([]);
      setCategories([]);

      alert('Note saved successfully!');
    } catch (error) {
      console.error('Error saving note:', error);
      alert('Error saving note. Please try again.');
    } finally {
      setIsProcessing(false);
    }
  };

  return (
    <div className="h-full w-full p-4 overflow-auto">
      <Card className="h-full flex flex-col">
        <CardHeader className="flex-shrink-0">
          <CardTitle className="flex items-center gap-2">
            <Bot className="w-5 h-5" />
            AI-Powered Note Taking
          </CardTitle>
          <CardDescription>
            Record voice notes or type manually. AI will analyze, summarize, and categorize your content automatically.
          </CardDescription>
        </CardHeader>
        <CardContent className="flex-1 flex flex-col space-y-6 overflow-hidden">

          <div className="flex-1 flex flex-col space-y-2">
            <label className="text-sm font-medium">Note Content</label>
            <Textarea
              value={manualText}
              onChange={(e) => setManualText(e.target.value)}
              placeholder="Type your note here..."
              className="flex-1 resize-none min-h-0"
            />
          </div>

          <div className="flex justify-end flex-shrink-0">
            <Button
              onClick={saveNote}
              disabled={isProcessing || !(manualText.trim() || transcript.trim())}
              className="flex items-center gap-2"
            >
              <Save className="w-4 h-4" />
              {isProcessing ? 'Saving...' : 'Save Note'}
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
